.table-results td span { position: relative; padding-left: 30px; display: inline-block; margin-bottom: 10px; }
.table-results td span [class^="icon-"] { position: absolute; top: 50%; margin-top: -15px; left: 0; }
.table-results td a { color: #5d5c5c; font-family: Arial, Helvetica, sans-serif; font-size: 15px; font-weight: 400; text-decoration: underline; }
.table-results td p { margin: 0 0 5px 0; }
.table-results td p span { padding: 0; color: #919191; font-size: 14px; }

.bottom { padding: 20px 0; }
.bottom tr { border-bottom: 1px solid #d0d0d0 !important; border-top: 1px solid #d0d0d0; }

.lnk { margin-bottom: 20px; }

.done { width: 100%; box-sizing: border-box; *zoom: 1; }
.done:after { content: " "; display: table; clear: both; }

.ansver { width: 100%; box-sizing: border-box; *zoom: 1; background-color: rgba(116, 182, 95, 0.2); padding: 35px 70px; -moz-placeholder-color: #5d5c5c; -ms-input-placeholder-color: #5d5c5c; }
.ansver:after { content: " "; display: table; clear: both; }
.ansver input[type="text"] { background-color: white; border: 2px solid #ebeced; border-radius: 3px; padding: 0 10px; width: 304px; margin: 0; }
